import React,{useState} from "react";
import { Doughnut } from "react-chartjs-2";
//import { MDBContainer } from "mdbreact";
import '../../App.css'

const ChartsPage= ({data1,data2,label1,label2,desc})=>{

 const [dataDoughnut,setDough]=useState( {
    labels: [label1,label2],
    datasets: [
      {
        data: [data1,data2],
        backgroundColor: [ "#949FB1","#FDB45C", "#4D5360"],
        hoverBackgroundColor: [
         "#FF5A5E",
          "#5AD3D1",
        ]
      }
    ]
  })
    return (
    <>
     <h3 className="mt-5">{desc}</h3>
      <Doughnut data={dataDoughnut} options={{ responsive: true }} className="chart"/>
      
     
    </>
    );
  
}

export default ChartsPage;