import React, { useState, useEffect, useContext } from 'react'
import '../../App.css'
import ChartsPage from './chart'
import { response,logins } from '../../App'
import { toast } from 'react-toastify'
import axios from 'axios'

export const Dashboard = () => {
    const [isloggedin,setisloggedin]=useContext(logins)
    const [data, setData] = useContext(response);
    const [fromValue, sfromvalue] = useState("")
    const [toValue, stovalue] = useState("")
    const logout=()=>{
        sessionStorage.clear()
        setisloggedin(false)
        toast.info("Logged Out!")
    }
   
    const searchfunc = () => {
        setData(null)
        axios.get(`https://www.ecoj.org.za/coj/api/v1/user/getdata`,{  headers: { Authorization: `Bearer ${sessionStorage.getItem('uux')}` } ,params:{from:new Date(fromValue.substring(0, 4), (fromValue.substring(5, 7) - 1), fromValue.substring(8, 10)).getTime(),to:new Date(toValue.substring(0, 4), toValue.substring(5, 7) - 1, toValue.substring(8, 10)).getTime()}})
      .then(rq=>setData(rq.data))
    
    }
    const reset = () => {
        setData(null)
        sfromvalue("")
        stovalue("")
        axios.get(`https://www.ecoj.org.za/coj/api/v1/user/getdata`,{  headers: { Authorization: `Bearer ${sessionStorage.getItem('uux')}` } })
      .then(rq=>setData(rq.data))
    
    }
    
    return (
        <div className="dashboard">From
            <input classname="date-input" type="date" value={fromValue} onChange={e =>  sfromvalue(e.target.value)} /> 
            To <input classname="date-input" type="date" value={toValue} onChange={e => stovalue(e.target.value)} />
            <span className="btn-search" onClick={searchfunc}>filter </span>
            <span className="btn-reset" onClick={reset}>clear </span>

            <fieldset>
                <legend>Data Summary</legend>
                <table>
                    <th>
                        Web Files
                    </th>
                    <th>
                        Pending Email Request
                    </th>
                    <th>
                        Pending SMS Request
                    </th>
                    <th>
                        Verified Files
                    </th>
                    <tr className="admin-tablerow">
                        <td>
                            {data!==null?data.webfilecount:""}
                        </td>
                        <td>
                            {data!==null?data.pendingemailrequestcount:""}
                        </td>
                        <td>
                        {data!==null?data.pendingsmsrequestcount:""}
                        </td>
                        <td>
                        {data!==null?data.verifiedfiles:""}
                        </td>
                    </tr>
                </table>
            </fieldset>
            <div className="chart">
                {data!==null&&<ChartsPage data1={data.total-data.webfilecount} data2={data.webfilecount} label1={"Total"} label2={"Webfile"} desc={"Webile"} />}
                {data!==null&&<ChartsPage data1={data.total-data.pendingemailrequestcount} data2={data.pendingemailrequestcount} label1={"Sent"} label2={"Pending"} desc={"Pending Email Requests"}/>}
                {data!==null&&<ChartsPage data1={data.total-data.pendingsmsrequestcount} data2={data.pendingsmsrequestcount} label1={"Sent"} label2={"Pending "} desc={"Pending SMS Requests"} />}
                {data!==null&&<ChartsPage data1={data.total-data.verifiedfiles} data2={data.verifiedfiles} label1={"Total"} label2={"unverified Files"} desc={"Verified Files"} />}
            </div>
            <br />
            <span className="logout" onClick={logout}>Logout</span>


        </div>
    )
}
