import './App.css';
import React, { useState, useEffect } from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Index from '../src/Component/index'
import PDF from '../src/Component/pdf'
import Admin from './Component/Admin';
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css';
import axios from 'axios'


export const customer = React.createContext()
export const logins = React.createContext()
export const response = React.createContext()
function App() {
  const [user, setUser] = useState({})
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [respData, setRespData] = useState(null)
  useEffect(() => {
    if(sessionStorage.getItem('uux')!==null){
      setIsLoggedIn(true)
      axios.get("https://www.ecoj.org.za/coj/api/v1/user/getdata",{  headers: { Authorization: `Bearer ${sessionStorage.getItem('uux')}` } })
      .then(r=>setRespData(r.data))
    }
   
  }, [])
  return (
    <customer.Provider value={[user, setUser]}>
      <logins.Provider value={[isLoggedIn, setIsLoggedIn]}>
        <response.Provider value={[respData, setRespData]}>
          <ToastContainer autoClose={1300} hideProgressBar={true} pauseOnHover={false} closeButton={false} />
          <Router>
            <Switch>
              <Route exact path={'/coj/:ref'} component={Index} />
              <Route path={'/coj/pdf/read'} component={PDF} />
              <Route path={'/coj/admin/'} component={Admin} />
            </Switch>
          </Router>
        </response.Provider >
      </logins.Provider>
    </customer.Provider>

  )
}
export default App;
